<template>
  <div id="dashboard">
    {{ roles }}
    <br>
    <br>
    <br>
    <button @click="logout">logout</button>
  </div>
</template>

<script>
export default {
  data () {
    return {
      roles: this.$store.getters.roles
    }
  },
  methods: {
    logout () {
      this.$store.dispatch('LogOut').then(() => {
        location.reload()
      })
    }
  }
}
</script>

<style scoped>

</style>



